.searchPack {
    display: flex;
    margin: 10px 0;
    flex-direction: row-reverse;
    width: 50vh;
    justify-content: space-around;
    align-items: center;
}

.search_table {
    z-index: 1;
}

.slider {
    width: 80%;
}

.paginator {
    margin: 10px;
}

.pageNumber {
    padding: 2px;
    border: 1px solid gray;
}

.selectedPage {
    font-weight: bold;
    border: 2px solid black;
}